
/*main container---------------------------------*/
.nav-container{
	background-color: black;
	padding: .3vw 0vw;
	position: sticky;
	top: 0;
	z-index: 2;
}


/*logo----------------------------------------------*/
.nav-logo-box{
	height: 5vw;
}
.nav-logo-box img{
	display: block;
	width: 100%;
	height: 100%;
	cursor: pointer;
}

/*soial links---------------------------------------*/
.nav-link-box{
	text-align: right;
}

.nav-social-icon{
	color: white;
	display: inline-block;
	font-size: 2.8vw;
	margin: 0vw 1.5vw;
	transition: .2s linear;
}

.nav-social-icon:hover{
	transform: scale(1.2);
	color: #5e17eb;

}

/*side-navbar-----------------------------------------------------------------*/
.top-nav-relative{
	position: relative;
	overflow: visible!important;
}
.side-nav-box{
	position: fixed;
	right: 1vw;
	top: 5.6vw;
	z-index: 2;
}

.side-nav-item-common{
	display: block;
	margin: .5vw;
	padding:.5vw .3vw ;
	font-size: 2vw;
	border: none;
	background-color: #5e17eb;
	color: white;
	border-radius: .5vw;
	transition: .2s linear;
	position: relative;
	box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
	
}

.side-nav-active{
	color: #5e17eb;
	color: white;
	background-color: black;
}

.side-nav-item-common svg{
	width: 4vw;
}
.side-nav-item-common::before{
	content: attr(data-before);
	position: absolute;
	left: -320%;
	top: 0;
	width: 15vw;
	display: none;
	font-family: 'Poppins',sans-serif;
	padding: .5vw 0vw;
	background-color: #5e17eb;
	color: white;
	border-radius: .5vw 0vw 0vw .5vw;
	transition: .2s linear;
}

.side-nav-item-common:hover{
	outline: .1vw solid #5e17eb;
	color: #5e17eb;
	background-color: white;
}
.side-nav-item-common:hover::before{
	display: block;
}


/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
	/*main container---------------------------------*/
	.nav-container{
		padding: 1vw 0vw;
		
	}
  /*logo----------------------------------------------*/
	.nav-logo-box{
		height: 11vw;
	}

/*	social links--------------------------------*/
	.nav-social-icon{
		font-size: 6vw;
		margin: 0vw 2vw;
		transition: .2s linear;
	}
/*	side nav----------------------------------------------*/
	.side-nav-box{
		right: 2vw;
		top: 13.6vw;
	}

	.side-nav-item-common{
		margin:1.2vw .5vw;
		padding:1.8vw 2vw ;
		font-size: 5vw;
		border-radius: 1.5vw;
	}

	.side-nav-item-common svg{
		width: 6vw;
	}
	.side-nav-item-common::before{
		left: -400%;
		top: 0;
		width: 40vw;
		display: none;
		border-radius: 1.5vw;
		padding: 1.6vw 0vw;
	}
}


